export default {
  SET_USER (state, user) {
    state.user = user
  },

  SET_USER_EMAIL (state, emailAddress) {
    state.user.username = emailAddress
  },

  SET_USER_PASSWORD (state, password) {
    state.user.password = password
  },

  RESET_USER (state) {
    state.user.name = ''
    state.user.password = ''
  }
}



