<template>
  <div class="card mx-0">
    <div class="card-header vertical-middle">
      <i class="fa fa-desktop"></i> Manage Screens
      <div class="float-right ml-2"  style="margin: -8px">
        <form action="" class="form-horizontal">
          <div class="form-group row mb-0">
            <div class="col-md-12">
              <input class="form-control" placeholder="Search screen" v-model="searhKeyword">
            </div>
          </div>
        </form>
      </div>

      <div class="btn-group float-right"  style="margin: -8px; margin-right:8px;">
        <router-link :to="{path: 'screens/create'}" class="btn btn-outline-secondary mt-0" title="Delete Selected Screens">
          <i class="fa fa-trash"></i>
        </router-link>
        <router-link :to="{path: 'screens/create'}" class="btn btn-outline-secondary mt-0" title="Restore Backup of Selected Screen">
          <i class="fa fa-undo"></i>
        </router-link>
        <router-link :to="{path: 'screens/create'}" class="btn btn-outline-secondary mt-0" title="Create New Screen">
          <i class="fa fa-file-o"></i>
        </router-link>
      </div>

    </div>

    <div class="card-body pb-1">
      <div id="view_list" class="row">
        <div class="col-lg-3 col-md-4 col-sm6" v-for="item in screens">
          <div class="card">
            <div class="card-header">
              <i class="fa fa-desktop"></i> {{ item.name }}
                <div class="float-right">
                  <b-button class="fa fa-gear btn btn-outline-secondary" style="margin: -8px"></b-button>
                </div>
            </div>
            <div class="card-body">
              <i class="bg-primary p-3 font-2xl mr-3 float-left" v-bind:class="item.icon"></i>
              <div class="text-muted font-xs">
                C : {{ moment(item.createdAt).format('DD-MM, HH:mm:ss') }}
                  <br>
                U : {{ moment(item.updatedAt).format('DD-MM, HH:mm:ss') }}
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="row">
        <div class="col-md-12">
          <hr>
          <b-pagination size="sm" :total-rows="amountOfScreens" :per-page="8" v-model="currentPage"></b-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import ScreensListMixin from './ScreensList.mixin'

  const ProductPatch = () => {
    import('./screen/ScreenPatch')
  }

  export default {
    mixins: [ScreensListMixin],

    components: {
      ProductPatch
    },

    methods: {
      showPatchForm (id) {
        this.$set(this, 'id', id)
        this.$store.commit('SET_IS_EDIT_PRODUCT_VISIBLE', true)
      },

      async deleteProduct (id) {
        await this.$store.dispatch('deleteProduct', id)
        this.$store.dispatch('getProductsByUser', this.user)
      }
    }
  }
</script>



