import { mapActions } from 'vuex'

export default {
  data: () => ({
    currentPage: 1,
    searhKeyword: undefined
  }),

  watch: {
    currentPage () {
      this.getScreens({
        search: this.searhKeyword,
        page: this.currentPage
      })
    },

    searhKeyword () {
      this.getScreens({
        search: this.searhKeyword,
        page: this.currentPage
      })
    }
  },

  created () {
    this.getScreens({
      search: this.searhKeyword,
      page: this.currentPage
    })
  },

  computed: {
    screens: {
      get () {
        return this.$store.state.Screens.screens
      }
    },

    amountOfScreens: {
      get () {
        return this.$store.state.Screens.filteredCollections
      }
    }

  },

  methods: {
    ...mapActions(['getScreens'])
  }
}



