<template>
  <div class="card">
    <b-form @submit="onSubmit" @reset="onReset" v-if="show">    
      <div class="card-header">
        <i class="fa fa-file-o"></i> Create New Screen
      </div>
      <div class="card-body">
          <b-form-group label="Screen Name:">
            <b-form-input type="email" v-model="form.email" required placeholder="Enter screen name">
            </b-form-input>
          </b-form-group>
          <b-form-group label="Icon:">
            <b-form-input type="text" v-model="form.name" required placeholder="Enter icon class">
            </b-form-input>
          </b-form-group>
          <b-form-group label="Security Level:">
            <b-form-select :options="foods" required v-model="form.food">
            </b-form-select>
          </b-form-group>
          <b-button type="submit" variant="primary"><i class="fa fa-save"></i> Save</b-button>
          <b-button type="reset" variant="danger"><i class="fa fa-eraser"></i> Clear</b-button>
      </div>
    </b-form>    
  </div>
</template>
<script>
  export default {
    data () {
      return {
        form: {
          email: '',
          name: '',
          food: null,
          checked: []
        },
        foods: [
          {
            text: 'Select One',
            value: null
          },
          'Carrots', 'Beans', 'Tomatoes', 'Corn'
        ],
        show: true
      }
    },
    methods: {
      onSubmit (evt) {
        evt.preventDefault()
        alert(JSON.stringify(this.form))
      },
      onReset (evt) {
        evt.preventDefault()
        /* Reset our form values */
        this.form.email = ''
        this.form.name = ''
        this.form.food = null
        this.form.checked = []
        /* Trick to reset/clear native browser form validation state */
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      }
    }
  }
</script>



