import Vue from 'vue'
import Router from 'vue-router'

/* ============================================================
 * 
 * Container
 * 
 * ============================================================ */
import Full from '@/containers/Full'
import ChefContainer from '@/containers/Chef'

/* ============================================================
 * 
 * Views
 * 
 * ============================================================ */
import Dashboard from '@/views/Dashboard'

/* ============================================================
 * 
 * Views - Icons
 * 
 * ============================================================ */
import FontAwesome from '@/views/icons/FontAwesome'
import SimpleLineIcons from '@/views/icons/SimpleLineIcons'

/* ============================================================
 * 
 * E N T R A N C E
 * 
 * ============================================================ */
import Page404 from '@/views/pages/Page404'
import Page500 from '@/views/pages/Page500'
import Login from '@/views/entrance/Login'
import Register from '@/views/pages/Register'

/* ============================================================
 * 
 * K I T C H E N S E T
 * 
 * ============================================================ */
import ChefScreens from '@/views/chef/gui-screens/ScreensList'
import ChefScreenCreate from '@/views/chef/gui-screens/screen/ScreenCreate'

import ChefNavbar from '@/views/chef/gui-navigation/list'

Vue.use(Router)

export default new Router({
  mode: 'history', // mode: 'hash' Demo is living in GitHub.io, so required!
  linkActiveClass: 'open active',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/',
      redirect: '/dashboard',
      name: 'Home',
      component: Full,
      children: [
        {
          path: 'dashboard',
          name: 'Dashboard',
          component: Dashboard
        },
        {
          path: 'icons',
          redirect: '/icons/font-awesome',
          name: 'Icons',
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path: 'font-awesome',
              name: 'Font Awesome',
              component: FontAwesome
            },
            {
              path: 'simple-line-icons',
              name: 'Simple Line Icons',
              component: SimpleLineIcons
            }
          ]
        }
      ]
    },
    {
      path: '/pages',
      redirect: '/pages/p404',
      name: 'Pages',
      component: {
        render (c) { return c('router-view') }
      },
      children: [
        {
          path: '404',
          name: 'Page404',
          component: Page404
        },
        {
          path: '500',
          name: 'Page500',
          component: Page500
        },
        {
          path: 'register',
          name: 'Register',
          component: Register
        }
      ]
    },
    {
      path: '/chef',
      name: 'Kitchen Set',
      redirect: '/chef/screens',
      component: ChefContainer,
      children: [
        {
          path: 'screens',
          name: 'Screens',
          component: {
            render (c) { return c('router-view') }
          },
          children: [
            {
              path: '',
              name: 'Manage Screens',
              component: ChefScreens
            },
            {
              path: 'create',
              name: 'Create New Screen',
              component: ChefScreenCreate
            }
          ]
        },
        {
          path: 'navigation',
          name: 'Navigation',
          component: ChefNavbar
        }
      ]
    },
    {
      path: '*',
      name: 'Undefined',
      redirect: '/dashboard'
    }
  ]
})



