<template>
  <router-view></router-view>
</template>

<style>
  // Import Font Awesome Icons Set
  $fa-font-path: "~font-awesome/fonts/";
  @import "~font-awesome/css/font-awesome.min.css";
  // Import Simple Line Icons Set
  $simple-line-font-path: "~simple-line-icons/fonts/";
  @import "~simple-line-icons/css/simple-line-icons.css";
</style>
<style lang="scss">
  // Import Main styles for this application
  @import "./scss/style";
</style>


<script>
  import { mapMutations } from 'vuex'
  import AppMixin from './App.mixin'

  const HelpIndex = () => import('./views/help/Help.desktop')

  export default {
    mixins: [AppMixin],

    components: {
      HelpIndex
    },

    data: () => ({
      io: null
    }),

    computed: {
      isHelpVisible: {
        get () {
          return this.$store.state.isHelpVisible
        },

        set (isHelpVisible) {
          this.store.commit('SET_IS_HELP_VISIBLE', isHelpVisible)
        }
      }
    },

    watch: {
      isHelpVisible () {
        if (!this.isHelpVisible) this.io.socket.disconnect()
        if (this.isHelpVisible && this.io) this.io.socket.reconnect()
      }
    },

    methods: {
      ...mapMutations({
        setIsHelpVisible: 'SET_IS_HELP_VISIBLE'
      })
    }
  }
</script>



