import Vue from 'vue'

export default {
  getScreens (context, args) {
    return new Promise((resolve, reject) => {
      Vue.http
        .get('/api/chef/screens',
          {
            params: {
              search: args.search,
              page: args.page,
              limit: 8
            }
          }
        )

        .then(({body}) => {
          context.commit('SET_SCREENS', body)
          resolve()
        })
        .catch(error => reject(error))
    })
  }

}



